@import "../../styles/_mixins"
@import "../../styles/_variables"
@import "../../styles/_keyframes"    
.goHome 
    position: fixed
    bottom: 5%
    right: 15%
    width: 50px
    height: 50px
    border: 3px solid $dark-gray
    border-radius: 50%
    +flex-parent
    transition: all .2s ease
    cursor: pointer
    svg
        transform: scale(2)
        fill: $dark-gray
        transition: all .2s ease
    &:hover
        animation: moveUpDown 2s linear infinite
        border: 3px solid $purple-plum
        svg
            .path
                fill: $purple-plum
               
     
